* {
  box-sizing: border-box;
}

body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  width: 100%;
  height: 200px;
}

.search-form {
  display: flex;
  justify-content: center;
  gap: 5px;

  padding: 5px;
  background-color: rgb(48, 48, 185);
}

.search-form__input {
  padding: 5px;
  border-radius: 5px;
  font-size: 16px;
}

.search-form__btn {
  padding: 5px;
  font-size: 16px;
  border-radius: 5px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;

  gap: 10px;
}

.info {
  display: flex;
  gap: 10px;

  padding: 5px;
}

.photo-card {
  flex-basis: calc((100% - 10px * 3) / 4);

  background-color: #efefef;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.info-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
